  font-weight: 400;
  line-height: 1.29;
}

.breed-select {
  border: 2px solid;
  margin-bottom: 20px;
  font-size: 20px;
  box-shadow: 10px 5px 5px gray;
}

.cat-info {
  max-width: 800px;
  border: 2px solid;
  align-items: center;
  gap: 20px;
  padding: 10px;
  display: flex;
  box-shadow: 10px 5px 5px gray;
}

.hidden {
  visibility: hidden;
}

.catText {
  flex-direction: column;
  display: flex;
}

.temperSpan {
  margin-right: 10px;
  font-size: 110%;
  font-style: italic;
  font-weight: bold;
}

.loader {
  width: 38px;
  height: 38px;
  box-sizing: border-box;
  border: 4px solid #0a0a0a;
  border-bottom-color: #e9eb96;
  border-radius: 50%;
  margin-left: 70px;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

